.resume-steps-wrapper{
    padding-bottom: 35px;
}
.resume-steps-wrapper .steps{
    display: flex;
    width: 710px;
    flex-wrap: wrap;
    margin: 0 auto;
    max-width: 100%;
}

.resume-steps-wrapper .steps .step{
    flex: 1;
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    z-index: 1;
}
.resume-steps-wrapper .steps .step::after{
    content: '';
    display: block;
    width: 100%;
    height: 1px;
    background-color: #DFE0DF;
    position: absolute;
    left: 50%;
    top: 13px;
    z-index: -1;
}
.resume-steps-wrapper .steps .step:last-child::after{
    display: none;
}
.resume-steps-wrapper .steps .step i{
    display: inline-block;
    width: 26px;
    height: 26px;
    border-radius: 50%;
    background-color: #DFE0DF;
    border: 2px solid #FFFFFF;
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
    line-height: 24px;
    text-align: center;
    font-size: 18px;
    color: #DFE0DF;
}

.resume-steps-wrapper .steps .step .step-title{
    font-size: 14px;
    line-height: 1.2;
    color: #B0B0B0;
    padding-top: 10px;
    text-align: center;
}

.resume-steps-wrapper .steps .step.step-completed::after{
    background-color: #219653;
}

.resume-steps-wrapper .steps .step.step-completed i{
    color: #fff;
    background-color: #219653;
    border-color: #219653;
    box-shadow: none;
}

.resume-steps-wrapper .steps .step.step-completed .step-title{
    color: #000000;
}

.back-btn{
    display: inline-block;
    font-size: 24px;
    margin-right: 15px;
}

.create-resume-step-content{
    position: relative;
}

.create-resume-step-content .resume-header{
    display: flex;
    align-items: center;
    padding: 20px 25px;
    border-bottom: 1px solid #D9E8FA;
}
.create-resume-step-content .resume-header h4{
    font-size: 24px;
    font-weight: 600;
    color: #080036;
}

.resume-body{
    padding: 40px 25px;
}

.upload-applicant-img{
    display: inline-block;
    margin-bottom: 30px;
}
.applicant-img{
    display: block;
    width: 125px;
    height: 125px;
    border-radius: 50%;
    border: 2px solid #2F80ED;
    margin-bottom: 10px;
}
.applicant-img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}
.upload-applicant-img-btn{
    position: relative;
}
.upload-applicant-img-btn input[type="file"]{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
}

.save-exit-btn{
    position: absolute;
    right: 15px;
    top: 20px;
}
.added-qualification-list{
    background-color: #fff;
    border: 1px solid #E7EBF0;
    padding: 30px 20px;
}
.qualification{
    position: relative;   
    padding: 20px 0;
    padding-right: 220px;
}
.qualification::before{
    content: "";
    display: inline-block;
    width: 2px;
    height: 46px;
    background-color: #2F80ED;
    position: absolute;
    left: -20px;
    top: 50%;
    transform: translateY(-50%);
}
.qualification .degree-name{
    display: block;
    font-size: 18px;
    line-height: 24px;
    color: #080036;
}
.qualification-metas{
    font-size: 14px;
    color: #B0B0B0;
    margin-left: -15px;
    margin-right: -15px;
}

.qualification .field{
    position: absolute;
    right: 0;
    top: 20px;
    font-size: 16px;
    color: #B0B0B0;
}

.qualification .text-btn{
    color: #FF0000;
    position: absolute;
    top: 40px;
    right: 0;
    padding: 0;
}


.qualification-metas > div{
    display: inline-block;
    padding: 0 15px;
    border-right: 1px solid #B0B0B0;
}

.qualification-metas > div:last-child{
    border-right: none;
}

.added-qualification-list .block-title{
    margin-bottom: 30px;
}

@media screen and (max-width: 767px){
    .save-exit-btn{
        position: static;
        margin-top: 30px;
    }
}

@media screen and (max-width: 479px){
    .resume-steps-wrapper .steps .step .step-title{
        font-size: 12px;
        line-height: 16px;
    }
    .resume-body{
        padding: 30px 20px;
    }
    .create-resume-step-content .resume-header{
        padding: 20px 20px;
    }
}

